import React from 'react';
import {Form, Button, Container, Row} from 'react-bootstrap';
import'./SignIn.css'


const SignIn = ({onRouteChange}) => {
	return (
		<Container className=' d-flex flex-wrap text-white text-center bordery  justify-content-center'>
		<Row className='p-3'>
				<Form className='' >
		  <Form.Group controlId="formBasicEmail">
		    <Form.Label className='bold'>Email address</Form.Label>
		    <Form.Control required={true}  className='bg-transparent text-white' type="email" placeholder="Enter your email" />
		    <Form.Text className="text-white-50">
		      We'll never share your email with anyone else.
		    </Form.Text>
		  </Form.Group>

		  <Form.Group controlId="formBasicPassword">
		    <Form.Label>Password</Form.Label>
		    <Form.Control required={true} className='bg-transparent text-white' type="password" placeholder="Enter your Password" />
		  </Form.Group>
		  <Form.Group controlId="formBasicCheckbox">
		    <Form.Check  type="checkbox" label="Remember me" />
		  </Form.Group>
		  <Button className='my-2' variant="outline-dark" type="submit" onClick={() => onRouteChange('home')}>
		    Sign In
		  </Button><br/>
		  <p onClick={() => onRouteChange('register')} className='link py-2'  >Register</p>
		</Form>
		</Row>
		</Container>);
}



export default SignIn;