import React from 'react';
import {Form, Button, Container, Row, Col} from 'react-bootstrap';

const Register = ({onRouteChange}) => {
	return (<Container className=' d-flex flex-wrap text-white text-center bordery  justify-content-center'>
		<Row className='p-3'>
		<Col md={12} xs={12}>
				<Form className='' >
		  <Form.Group controlId="formBasicName">
		    <Form.Label className='bold'>User Name</Form.Label>
		    <Form.Control  className='bg-transparent text-white' type="text" placeholder="User Name" />
		   
		  </Form.Group>  
		  <Form.Group controlId="formBasicEmail">
		    <Form.Label className='bold'>Email address</Form.Label>
		    <Form.Control  className='bg-transparent text-white' type="email" placeholder="Enter your email" />
		    <Form.Text className="text-white-50">
		      We'll never share your email with anyone else.
		    </Form.Text>
		  </Form.Group>

		  <Form.Group controlId="formBasicPassword">
		    <Form.Label>Password</Form.Label>
		    <Form.Control className='bg-transparent text-white' type="password" placeholder="Enter your Password" />
		  </Form.Group>
		  <Form.Group controlId="formBasicCheckbox">
		    <Form.Check type="checkbox" label="already have an account ?" />
		  </Form.Group>
		  <Button className='my-2' variant="outline-dark" type="submit" onClick={() => onRouteChange('home')}>
		  Register
		  </Button>
		</Form>
		</Col>
		</Row>
		</Container>);
}




export default Register;



